import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SharedService {

  private userPic = new BehaviorSubject<any>("");
  userpiccast = this.userPic.asObservable();

  private isApartmenSelected = new BehaviorSubject<boolean>(false);
  apartmentselectedcast = this.isApartmenSelected.asObservable();

  private apartmentPic = new BehaviorSubject<any>("");
  apartmentpiccast = this.apartmentPic.asObservable();

  constructor() { }

  setApartmentSelected(value){
    this.isApartmenSelected.next(value);
  }

  setUserPic(value){
    this.userPic.next(value)
  }

  setApartmentPic(value){
    this.apartmentPic.next(value)
  }

}
